import Date from "./Date";
const Dates = (props) => {
    let dates = props.dates;
    let addDate = props.addDate;
    let formDisplay = {};
    if (addDate === false) {
        formDisplay = {
            display: 'none'
        }
    }else{
        formDisplay = {
            display: 'block'
        }
    }
    return ( 
        <div class="Dates">
            <button type='submit' onClick={props.onToggleAddDate} 
                  class="my-2 btn btn-primary float-end">{addDate == false ? "Ajouter une date" : "Fermer"}</button>
                <h2>Dates</h2>
                
                  <form style={formDisplay} className='addDate' action="" >
                        <div  class="col-md-6">
                        <label for="example-date-input" class="col-2 col-form-label">Date</label>
                        <div class="col">
                            <input class="form-control" type="date" id="example-date-input"/>
                        </div>
                        </div>
                        <div class="col-md-6">
                            <label for="inputEmail4" class="form-label">Ville</label>
                            <input type="text" class="form-control" id="inputEmail4"/>
                        </div>
                        <div class="col-md-6">
                            <label for="inputEmail4" class="form-label">Salle</label>
                            <input type="text" class="form-control" id="inputEmail4"/>
                        </div>
                        <div class="col-md-6">
                            <label for="inputEmail4" class="form-label">Tarifs</label>
                            <div class="row">
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Catégorie 1</label>
                          <input type="text" class="name form-control" placeholder="Prix" aria-label="First name"  required/>
                        </div>
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Catégorie 2</label>
                          <input type="text" class="poste form-control" placeholder="Prix" aria-label="Last name" required/>
                        </div>
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Fosse</label>
                            <input type="text" class="email form-control" placeholder="Prix" aria-label="Last name" required/>
                          </div>
                      </div>
                        </div>
                        <div class="col-12">
                            <button onClick={props.onToggleSubmitDate} type="submit" class="mt-3 btn btn-primary">Ajouter</button>
                        </div>

                  </form>
                <table class="table table-striped">
                    <thead>
                      <tr>
                        <th scope="col">#</th>
                        <th scope="col">Date</th>
                        <th scope="col">Ville</th>
                        <th scope="col">Salle</th>
                        <th scope="col">Tarifs</th>
                        <th scope="col">Paramètres</th>
                      </tr>
                    </thead>
                    <tbody class="table">
                         {dates.map((date, index) => {
                             return <Date
                             onToggleUpdateDate={props.onToggleUpdateDate}
                             onToggleDeleteDate={props.onToggleDeleteDate} 
                             key={index}
                             {...date}
                             index={index}
                             />
                         })}
                    
                    </tbody>
                  </table>
                  
                  
            </div>
     );
}
 
export default Dates;