const Date = (props) => {
  
    let dateInfo = '';
    if (props.isEditing === false) {
        dateInfo = (<tr>
                <th  scope="row">{props.index + 1}</th>
                <td>{props.date}</td>
                <td>{props.city}</td>
                <td>{props.place}</td>
                <td>
                    <table>
                        <thead>
                            <tr>
                                <th scope="col" className='px-3'>Catégorie 1</th>
                                <th scope="col" className='px-3'>Catégorie 2</th>
                                <th scope="col" className='px-3'>Fosse</th>
                            </tr>
                            <tr>
                                <td className='px-3'>{props.prices.cat1} euros</td>
                                <td className='px-3'>{props.prices.cat2} euros</td>
                                <td className='px-3'>{props.prices.cat3} euros</td>
                            </tr>
                        </thead>
                    </table>
                
                </td>
                <td>
                <button onClick={() => {
                    props.onToggleUpdateDate(parseInt(props.index));
                }} type="button" class="fas fa-edit btn btn-outline-dark"></button>
                <button  onClick={() => {
                    props.onToggleDeleteDate(parseInt(props.index));
                }} type="button" class="fa fa-trash btn btn-outline-danger"></button>
                </td>
            </tr>);
    }
    if (props.isEditing === true){
        dateInfo = (
            <tr className='dateTable'>
                <th  scope="row">{props.index + 1}</th>
                <td>
                    <input class="form-control" type="date" id="example-date-input" defaultValue={props.date}/>
                </td>
                <td>
                    <input type="text" class="form-control" id="inputEmail4" defaultValue={props.city}/>    
                </td>
                <td>
                    <input type="text" class="form-control" id="inputEmail4" defaultValue={props.place}/>    
                </td>
                <td>
                    <table>
                        <thead>
                            <tr>
                                <th scope="col" className='px-3'>Catégorie 1</th>
                                <th scope="col" className='px-3'>Catégorie 2</th>
                                <th scope="col" className='px-3'>Fosse</th>
                            </tr>
                            <tr>
                                <td className='px-3'><input type="text" class="name form-control" placeholder="Prix" aria-label="First name"  required defaultValue={props.prices.cat1}/></td>
                                <td className='px-3'><input type="text" class="name form-control" placeholder="Prix" aria-label="First name"  required defaultValue={props.prices.cat2}/></td>
                                <td className='px-3'><input type="text" class="name form-control" placeholder="Prix" aria-label="First name"  required defaultValue={props.prices.cat3}/></td>
                            </tr>
                        </thead>
                    </table>
                
                </td>
                <td>
                <button onClick={() => {
                    props.onToggleUpdateDate(parseInt(props.index));
                }} type="button" class="fas fa-edit btn btn-outline-dark"></button>
                <button  onClick={() => {
                    props.onToggleDeleteDate(parseInt(props.index));
                }} type="button" class="fa fa-trash btn btn-outline-danger"></button>
                </td>
            </tr>
        );
    }
    return ( 
        <>
            {dateInfo}
        </>
     );
}
 
export default Date;